.post {
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-clip: white;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}

.post__image>img {
    width: 100%;
}

.post__top {
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}
.post__avatar {
    margin-right: 10px;
}

.post__topInfo>h3 {
    font-size: medium;
}

.post__topInfo>p {
    font-size: small;
    color: gray;
    margin-bottom: 0;
}
.post__bottom {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 25px;
}

.post__options {
    padding: 10px;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid rgba(211, 211, 211, 0.781);
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: gray;
    cursor: pointer;
    padding: 10px;
}

.post__option {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    float: 1;
}
.post__option>p {
    margin-left: 10px;
    margin-bottom: 0;
}

.post__option:hover {
    background-color: #eff2f5;
    border-radius: 10px;
}
.post__box{
    box-shadow: 0px 5px 7px -7px rgb(0 0 0 / 75%);
    margin-top: 1.2rem;
    border-radius: 15px;
    background: white;
}
.imgName img{
    height: 400px;
    width: 100%;
}
p.colp{
    outline-width: 0px;
    border: none;
    padding: 6px 20px;
    margin: 0px 10px;
    border-radius: 999px;
    background-color: rgb(239, 242, 245);
}
div.com1{
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
    padding-top: 1px;
}