div.messageSender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
     width: 100%; 
}
.m__top{
    display: flex;
    /* border-bottom: 1px solid #eff2f5; */
    padding-bottom:15px;
    padding:15px
}
div.message1.input.form-control{
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0 20px;
    border-radius: 999px;
    background-color: #eff2f5;
}
.messageSender__option {
    padding: 20px;
    display: flex;
    align-items: center;
    color: gray;
    margin: 5px;
}

.messageSender__bottom {
    display: flex;
    justify-content: space-evenly;
}
.messageSender__option>h3 {
    font-size: medium;
    margin-left: 10px;
}

.messageSender__option:hover {
    background-color: #eff2f5;
    border-radius: 20px;
}